.note-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.note-form__title {
  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 96px;
  width: 80vw;
  text-align: center;
  letter-spacing: -0.02em;

  color: #0e1111;

  margin-top: 2rem;
  background-color: transparent;
  outline: 0;

  border: none;
  border-bottom: solid 1px var(--secondary);
}

.note-form__title:focus {
  text-decoration: none;
}

.note-subheader__container {
  display: flex;
  justify-content: space-between;
  padding: 1rem 11vw;
  width: 80vw;
  border-bottom: solid 1px var(--secondary);
}

.form-date {
  font-style: italic;
  font-weight: 300;
  font-size: 24px;
  line-height: 30px;
  text-align: right;
  letter-spacing: 0.07em;

  /* Secondary Color */

  color: #0e1111;
}

.note-form__notes {
  margin-top: 0.5rem;
  resize: none;
  outline: 0;
  width: 60vw;
  height: 60vh;

  font-weight: 300;
  font-size: 24px;
  line-height: 261.7%;

  letter-spacing: 0.07em;

  color: var(--secondary);
  padding: 0 20px;

  background-color: transparent;
  outline: 0;

  border: none;
}

.note-form__save {
  height: 90px;
  width: 90px;
  border-radius: 50%;
  outline: 0;
  border: 1px solid var(--secondary);

  position: fixed;
  bottom: 10%;
  right: 10%;
  background-color: var(--accent-2);
  color: var(--secondary);
}

.note-form__save.unsaved {
  color: var(--accent-2);
  background-color: var(--secondary);
}

.note-error {
  font-family: "Lato";
  font-style: normal;
  font-weight: 300;
  font-size: 15px;
  line-height: 18px;
  /* identical to box height */

  text-align: center;
  letter-spacing: 0.19em;
  background-color: #0e111118;
  padding: 20px;
  text-transform: uppercase;

  /* Secondary Color */

  color: var(--accent-1);
}

@media only screen and (max-width: 435px) {
  .note-form__title {
    font-size: 36px;
  }

  .note-subheader__container {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .favorite-container {
    width: 100px;
  }

  .favorite-span {
    font-size: 12px;
    width: 100%;
  }

  .favorite-label {
    width: 100%;
  }

  .form-date {
    margin-top: 20px;
    font-size: 12px;
  }
  .note-form__notes {
    font-size: 16px;
    width: 90vw;
  }
}
