.note-single-page {
  background-color: var(--accent-2);
  height: 100vh;
  width: 100vw;

  position: absolute;
  left: 0;
}

.note__module-container {
  background-color: #0e1111bd;
  height: 100vh;
  width: 100vw;
  position: absolute;
  top: 0;

  display: flex;
  justify-content: center;
  align-items: center;
}

.note__module-box {
  background-color: var(--accent-2);
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 3px;
}

.note__module-text {
  font-weight: 300;
  font-size: 24px;
  line-height: 261.7%;
  letter-spacing: 0.07em;
  color: #0e1111;
}
