.edit-form {
  background-color: #0e1111de;
  height: 100vh;
  width: 100vw;
  position: absolute;
  top: 0;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.edit-form__container {
  background-color: var(--accent-2);
  padding: 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 3px;
  width: 350px;
  overflow: hidden;

  box-shadow: rgba(255, 255, 255, 0.12) 0px 1px 3px,
    rgba(255, 255, 255, 0.24) 0px 1px 2px;
}

.edit__top {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  background-color: var(--secondary);
  padding: 10px;
  margin-bottom: 20px;
}
.edit__title {
  font-family: "Lato";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.265em;

  /* Secondary Color */

  color: var(--accent-2);
}

.edit-form .form__input {
  background: var(--accent-2);

  border: 1px solid var(--secondary);

  color: var(--secondary);

  margin: 10px 0 0 0;
  width: 100%;
}

.edit__task-input:focus {
  background: var(--primary);
}

.edit-form .favorite-container {
  width: 280px;
  margin-bottom: 20px;
}

.edit-form .favorite-label {
  width: 100%;
}

.edit-form .favorite-span {
  width: 100%;
}

.edit-buttons {
  display: flex;
  width: 100%;
  padding: 20px 5%;
  border-top: 1px solid var(--secondary);
  background-color: #f7e173;
}

.edit__submit,
.edit__exit {
  width: 98px;
  height: 40px;
  left: 1074px;
  top: 22px;

  border-radius: 1px;

  font-family: "Lato";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.265em;

  cursor: pointer;
}

.edit__submit {
  margin-top: auto;
  border: 1px solid var(--secondary);
  background-color: var(--secondary);
  color: var(--accent-2);
  flex-grow: 1;
}

.edit__submit:disabled {
  border: 1px solid var(--secondary);
  background-color: rgba(0, 0, 0, 0.441);
  color: var(--accent-2);
  flex-grow: 1;
  font-size: 12px;
}

.edit__exit {
  margin-top: auto;
  border: 1px solid var(--secondary);
  background-color: var(--accent-2);
  color: var(--secondary);
  flex-grow: 1;
}

.edit__task-label {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;

  width: 80%;

  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 800;
  font-size: 14px;
  line-height: 18px;

  letter-spacing: 0.25em;

  color: var(--secondary);

  margin-bottom: 20px;
}

/* EXIT! */

.todo-edit__exit:hover {
  scale: 1.5;
}
