@import url("https://fonts.googleapis.com/css2?family=Merriweather+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Merriweather+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap");

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px var(--secondary) inset !important;
  -webkit-text-fill-color: var(--primary) !important;
}
input:-webkit-autofill:focus {
  -webkit-box-shadow: 0 0 0 30px var(--primary) inset !important;
  -webkit-text-fill-color: var(--secondary) !important;
}

:root {
  color-scheme: dark;
  --primary: #ffd300;
  --secondary: #0e1111;
  --accent-1: #e32017;
  --accent-2: #ffe97f;
  --accent-1-transparent: #e32117a7;
}

* {
  box-sizing: border-box;
}

body {
  font-family: "Merriweather Sans", sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0;
  width: 100vw;

  overflow-x: hidden;
  background-color: transparent;
}

/* form defaults */

.form__label {
  color: var(--primary);
  font-weight: 800;
  letter-spacing: 0.25em;
}

.form__input {
  width: 100%;
  outline: 0;
  padding: 17.5px;
  background: transparent;
  margin: 15px 0;
  border: 1px solid var(--primary);

  color: var(--primary);
  font-size: 15px;
  letter-spacing: 0.07em;
  color: co;

  border-radius: 4px;
}

.form__input:focus {
  background: var(--primary);
  border: 1px solid var(--secondary);
  color: var(--secondary);
}

.form__input::placeholder {
  font-family: "Merriweather Sans";
  color: var(--primary);
  font-weight: 200;
  font-size: 12px;
  line-height: 19px;
  letter-spacing: 0.07em;
}

.form__btn--submit {
  background-color: var(--primary);
  font-family: "Lato";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.265em;
  height: 50px;
  transition: box-shadow 0.15s, transform 0.15s;
  margin: 30px 0 0 0;
  color: var(--secondary);
  border: none;
  border-radius: 4px;
}

.form__btn--submit:hover {
  background-color: var(--primary);
  transform: translateY(-0.5px);
  filter: brightness(92%);
  cursor: pointer;
}

.form__btn--submit:active {
  background-color: var(--primary);
  transform: translateY(0.5px);
}

.invalid.form__input:focus {
  background: var(--accent-1-transparent);
  color: var(--secondary);
}

.invalid.form__input::placeholder {
  color: var(--accent-1-transparent);
}
.invalid.form__input {
  border: 1px solid var(--accent-1);
  color: var(--accent-1);
}

/* nav-1 (public) */
.nav-1 {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: 80px;
  background-color: var(--primary);
  width: 100vw;
  position: absolute;
  top: 0;
  padding: 0 8%;
}

.nav-1__link {
  text-decoration: none;

  background-color: transparent;
  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 800;
  font-size: 28px;
  line-height: 60px;
  letter-spacing: 0.25em;
  color: var(--secondary);
}

.nav-1__link:hover {
  scale: 1.01;
}

.nav-1__cta-1 {
  width: 98px;
  height: 40px;
  left: 1074px;
  top: 22px;
  border-radius: 1px;
  font-family: "Lato";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.265em;

  background-color: transparent;
  border: 1px solid #ffd300;
  color: #ffd300;
}

.nav-1__cta-1:hover {
  background: #ffd300;
  color: #000000;
  cursor: pointer;
}
.nav-1__cta-2:hover {
  background: var(--secondary);
  color: #ffd300;
  cursor: pointer;
}

.nav-1__cta-2 {
  margin-left: 20px;
  width: 98px;
  height: 40px;
  left: 1200px;
  top: 22px;
  border-radius: 1px;
  font-family: "Lato";
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  letter-spacing: 0.265em;

  border: 1px solid var(--primary);
  background: var(--primary);
  color: var(--secondary);
}

/* nav-2 (private) */
.nav-2 {
  background-color: transparent;
  margin: 0 10vw;
  height: 70px;
  display: flex;
  border-bottom: solid 1px var(--secondary);

  align-items: flex-end;
  justify-content: flex-end;
}
.nav-2__close {
  text-decoration: none;
  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 100%;
  display: flex;
  align-items: center;
  letter-spacing: -0.025em;
  margin: 0 15px 15px 0;
  color: var(--secondary);

  cursor: pointer;
}

/* nav-2  cta buttons */
.module-yes,
.module-no {
  width: 98px;
  height: 40px;
  left: 1074px;
  top: 22px;
  margin-bottom: 20px;

  border-radius: 1px;

  font-family: "Lato";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.265em;
}

.module-yes {
  border: 1px solid var(--secondary);
  background-color: var(--secondary);
  color: var(--accent-2);
}

.module-yes:hover,
.module-no:hover {
  background: #ffd300;
  color: #000000;
  cursor: pointer;
}

.module-no {
  border: 1px solid var(--secondary);
  background-color: transparent;
  color: var(--secondary);
}

/* favorite/s button - notes*/

.favorite-container {
  display: flex;
  width: auto;
  border: var(--secondary) 1px solid;
}

.favorite-label {
  cursor: pointer;
  margin: 0;
  padding: 0;
}

.favorite-check {
  display: none;
}

.favorite-check:checked ~ span {
  background-color: var(--secondary);
  color: var(--accent-2);
}

.favorite-span {
  position: relative;
  display: inline-block;
  background-color: transparent;

  padding: 10px;

  font-family: "Lato";
  font-style: normal;
  font-weight: 300;
  font-size: 15px;
  line-height: 18px;
  cursor: pointer;

  text-align: center;
  letter-spacing: 0.19em;

  color: #0e1111;

  margin: 0;

  user-select: none;
}

/* filter search */
.filter__container {
  display: flex;
  justify-content: center;
  align-items: center;

  margin-bottom: 15px;
  width: 40vw;
}
.filter__input {
  width: 100%;
  outline: 0;
  padding: 17.5px;
  background: var(--accent-2);

  border: 1px solid var(--primary);

  color: var(--secondary);
  font-size: 15px;
  letter-spacing: 0.07em;
}

.filter__button {
  width: 78px;
  height: 50px;
  margin: 0;

  background-color: var(--secondary);

  border: 0;
  outline: 0;

  font-family: "Merriweather Sans";

  font-weight: 300;
  font-size: 15px;
  line-height: 19px;

  letter-spacing: 0.05em;

  /* Accent Color 2 */

  color: #ffe97f;

  cursor: pointer;
}

/* page mode titles */
.page__title {
  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 180px;
  line-height: 226px;
  letter-spacing: 0.19em;

  color: var(--secondary);
}

/* footer */
.footer__container {
  z-index: 1;
  left: 0;
  width: 100vw;
  height: 70px;
  position: fixed;
  bottom: 0;
  background-color: var(--accent-2);
  opacity: 0.9;
  color: var(--secondary);

  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 100%;

  display: flex;
  align-items: center;
  letter-spacing: 0.325em;

  color: #000000;
}

.footer__empty {
  height: 80px;
}

@media only screen and (max-width: 435px) {
  .page__title {
    font-weight: 700;
    font-size: 54px;
    line-height: 126px;
    letter-spacing: 0.19em;
  }

  .filter__container {
    display: flex;
    justify-content: center;
    align-items: center;

    margin-bottom: 15px;
    width: 80vw;
  }

  .nav-2__close {
    font-size: 16px;
  }
}
