html {
  overflow-x: hidden;
}

.home-page {
  background-color: var(--primary);
  height: auto;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;

  left: 0;
  position: absolute;

  overflow-x: hidden;
}

.home-page .nav-1 {
  background-color: var(--secondary);
  justify-content: space-between;

  overflow: hidden;
  z-index: 2;
}

.home-page .nav-1__link {
  color: var(--primary);
}

.home-page .header__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.home-page .header__main {
  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 800;
  font-size: 11.4rem;
  line-height: 226px;
  letter-spacing: 0.25em;
  margin-top: 85px;
  margin-bottom: 0;
  color: var(--secondary);
}

.home-page .subheader__container {
  margin: 0;
  width: 60%;
}
.home-page .subheader__quote {
  margin-top: 0;
  font-family: "Lato";
  font-style: italic;
  font-weight: 400;
  font-size: 20px;
  line-height: 130%;
  letter-spacing: 0.135em;
  text-align: justify;

  color: var(--secondary);
}
.home-page .subheader__author {
  margin-top: 0;
  font-family: "Lato";
  font-style: italic;
  font-weight: 400;
  font-size: 16px;
  line-height: 175%;
  text-align: right;
  letter-spacing: 0.155em;
  color: var(--secondary);
}

.home-page .cta__container {
  display: flex;
  align-items: center;
  width: 100%;
}

.home-page .cta__button--signup {
  width: 175px;
  height: 40px;
  left: 142px;
  top: 433px;

  background: var(--accent-1);
  border: 1px solid #000000;
  border-radius: 51px;

  font-family: "Lato";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.265em;

  /* Secondary Color */

  color: var(--secondary);

  margin-right: 25px;
}

.home-page .cta__button--signup:hover {
  color: var(--accent-2);
  background: var(--secondary);
  cursor: pointer;
}

.home-page .cta__button--login:hover {
  color: var(--primary);
  background: var(--secondary);
  cursor: pointer;
}

.home-page .cta__button--login {
  width: 176px;
  height: 40px;
  left: 337px;
  top: 433px;

  /* Secondary Color */
  background: transparent;
  border: 1px solid #0e1111;
  border-radius: 51px;

  font-family: "Lato";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.265em;

  /* Secondary Color */

  color: var(--secondary);

  margin-right: 25px;
}
.home-page .cta__line {
  flex-grow: 1;
  border-bottom: 1px black solid;
}
.home-image-container {
  width: 100%;
  position: relative;
  display: flex;
}

.home-image {
  margin-top: 50px;
  width: 100%;
  height: 365px;
  left: 140px;
  top: 529px;
  object-fit: cover;

  border-radius: 45px 45px 0px 0px;
}

.home-version {
  position: absolute;
  width: 152px;
  height: 152px;
  right: 50px;
  top: 0;

  display: flex;
  justify-content: center;
  align-items: center;

  background: var(--accent-1);
  /* Secondary Color */

  border: 1px solid #0e1111;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 50%;
}

.home-version > div {
  font-family: "Merriweather Sans";
  padding-left: 20px;
  font-style: normal;
  font-weight: 800;
  font-size: 64px;
  line-height: 80px;
  text-align: center;
  letter-spacing: 0.265em;
  user-select: none;

  color: var(--secondary);
}

.home-sliding-text__container {
  background-color: var(--secondary);
  height: 126px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  overflow-x: hidden;
  width: 100vw;
  flex-wrap: nowrap;
}
.home-sliding-text__text {
  color: var(--primary);
  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 800;
  font-size: 48px;
  line-height: 100%;
  letter-spacing: 0em;
  color: var(--primary);
  white-space: nowrap;
  margin: 0;
}

.features__heading {
  border-bottom: #0e1111 solid 1px;
}

.features__heading-row1 {
  display: flex;
  justify-content: space-between;

  width: 1200px;

  margin-top: 45px;

  align-items: center;
}

.features__headline,
.features__headline-dot {
  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 800;
  font-size: 96px;
  line-height: 100%;
  /* identical to box height, or 96px */

  letter-spacing: -0.025em;

  /* Secondary Color */

  color: #0e1111;
}
.features__headline-dot {
  color: var(--accent-1);
}

.features__cta:hover {
  color: var(--accent-2);
  background: var(--secondary);
  cursor: pointer;
}

.features__cta {
  margin-left: auto;
  width: 275px;
  height: 40px;
  left: 1025px;
  top: 1073px;

  /* Accent Color 1 */

  background: #e32017;
  border: 1px solid #000000;
  border-radius: 51px;

  font-family: "Lato";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.265em;

  /* Secondary Color */

  color: #0e1111;
}

.features__headline-paragraph {
  width: 472px;
  height: 84px;
  left: 140px;
  top: 1157px;

  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 197%;
  /* or 28px */

  letter-spacing: 0.07em;

  color: #000000;
}

.features__notes__container {
  display: flex;
  border-bottom: var(--secondary) 1px solid;
  width: 1200px;
}

.features__notes,
.features__notes-dot {
  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 300;
  font-size: 128px;
  line-height: 161px;
  letter-spacing: 0.07em;

  /* Secondary Color */

  color: #0e1111;
}

.features__notes-dot {
  color: var(--accent-1);
}

.ready__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  width: 100vw;
  height: 1072px;
  left: 0px;
  top: 1852px;
  background-color: var(--secondary);
}

.ready__text {
  width: 761px;
  height: 378px;
  left: 343px;
  top: 2083px;

  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 800;
  font-size: 128px;
  line-height: 100%;
  /* or 128px */

  text-align: center;

  letter-spacing: -0.025em;

  /* Accent Color 2 */

  color: #ffe97f;
}
.ready__cta__button--login,
.ready__cta__button--signup {
  width: 175px;
  height: 40px;
  left: 142px;
  top: 433px;

  margin-top: 2%;

  background: var(--accent-1);
  border: 1px solid #000000;
  border-radius: 51px;

  font-family: "Lato";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.265em;

  /* Secondary Color */

  color: var(--secondary);

  margin-right: 25px;
}
.ready__cta__button--login {
  color: var(--accent-2);
  background: var(--secondary);
  border-color: var(--accent-2);
  cursor: pointer;
  scale: 0.8;
}

.ready__cta__button--signup:hover {
  background-color: var(--secondary);
  color: var(--accent-1);
  border-color: var(--accent-1);
  cursor: pointer;
}

.ready__cta__button--login:hover {
  background-color: var(--accent-2);
  color: var(--secondary);
}

footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 251px;
  background-color: var(--accent-1);
  width: 100%;
}

.designed-by {
  font-weight: 300;
  font-size: 13px;
  line-height: 100%;
  /* or 13px */

  letter-spacing: -0.025em;

  /* Accent Color 2 */

  color: #ffe97f;
}

.janille {
  margin-top: 30px;
  font-weight: 700;
  font-size: 24px;
  line-height: 100%;
  /* or 24px */

  letter-spacing: 0.395em;

  /* Accent Color 2 */

  color: #ffe97f;
}

footer > .email {
  margin: 20px;
  font-weight: 300;
  font-size: 15px;
  line-height: 100%;
  /* or 15px */

  letter-spacing: 0.595em;

  /* Accent Color 2 */

  color: #ffe97f;
}

@media only screen and (max-width: 1024px) {
  .home-page .header__main {
    font-size: 8.4rem;
    letter-spacing: 1rem;
    margin-top: 80px;
  }
  .cta__container {
    width: 80%;
  }
  .features__heading,
  .features__notes__container {
    width: 85vw;
  }
  .features__heading-row1 {
    width: 100%;
  }
  .features__cta {
    font-size: 12px;
  }

  .home-page .cta__container {
    width: 90vw;
  }
}

@media only screen and (max-width: 435px) {
  .home-page {
    height: auto;
  }
  .home-page .nav-1 {
    height: 40px;
    justify-content: flex-end;
    padding: 0;
  }

  .home-page .nav-1__link {
    display: none;
  }

  .home-page .nav-1__cta-1 {
    font-size: 12px;
    border: none;
  }
  .home-page .nav-1__cta-2 {
    font-size: 12px;
  }

  .home-page .header__main {
    font-size: 4rem;
    letter-spacing: 0.2rem;
    margin-top: 0;
  }

  .home-page .cta__container {
    flex-direction: column;
  }

  .home-page .cta__button--signup {
    margin: 0;
    margin-bottom: 20px;
  }
  .home-page .cta__button--login {
    margin: 0;
  }

  .home-version {
    width: 82px;
    height: 82px;
    right: 10px;

    top: -5px;
  }
  .home-version > div {
    font-size: 44px;
    padding-left: 8px;
  }
  .home-image {
    border-radius: 0;
    height: 165px;
  }

  .features__heading-row1 {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 50px 0 30px 0;

    width: 100%;
  }
  .features__headline {
    font-size: 4.4rem;
  }
  .features__headline-dot {
    display: none;
  }

  .features__cta {
    display: none;
    width: 200px;
    height: 80px;
    margin: 20px 0;
    border-radius: 10px;
  }
  .features__headline-paragraph {
    height: auto;
    width: 100%;
    margin-bottom: 50px;

    padding: 0 20px;
    text-align: justify;
  }
  .features__notes,
  .features__notes-dot {
    font-size: 80px;
  }
  .features__heading,
  .features__notes__container {
    width: 85vw;
  }
  .features__notes__container:last-of-type {
    display: none;
  }
  .ready__container {
    padding: 100px 0;
    height: auto;
  }
  .ready__text {
    font-size: 4rem;
    width: 80vw;
    height: auto;
    margin-bottom: 20px;
  }
}

@media only screen and (min-width: 2560px) {
  .home-image {
    height: 650px;
  }
  .home-page .cta__button--signup {
    scale: 1.2;
  }
  .home-page .cta__button--login {
    scale: 1.2;
    margin: 0 50px;
  }
  .ready__container {
    height: 1200px;
  }
  .ready__text {
    font-size: 200px;
    width: 900px;
    height: auto;
  }
  .ready__cta__button--login,
  .ready__cta__button--signup {
    margin: 20px;
    scale: 1.5;
  }
  .ready__cta__button--signup {
    margin-top: 80px;
  }
  .features__cta {
    scale: 1.5;
  }

  .features__headline,
  .features__headline-dot {
    font-size: 120px;
  }
}
