.error-page {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  background-color: var(--secondary);

  position: absolute;
  left: 0;
}

.error-page__header {
  color: var(--primary);
  font-family: "Lato";
  font-style: normal;
  font-weight: 700;
  font-size: 180px;
  line-height: 216px;

  letter-spacing: 0.1em;

  opacity: 0.8;

  height: 160px;
  user-select: none;

  padding-left: 10px;
}

.error-page__text-1 {
  color: var(--accent-1);
  font-family: "Lato";
  font-style: normal;
  font-weight: 300;
  font-size: 96px;
  line-height: 131.7%;

  text-align: center;
  letter-spacing: 0.07em;

  height: 100px;

  color: #ffe97f;
  opacity: 0.4;

  user-select: none;
}

.error-page__text-2 {
  font-family: "Lato";
  font-style: normal;
  font-weight: 300;
  font-size: 24px;
  line-height: 131.7%;

  text-align: center;
  letter-spacing: 0.07em;

  color: #ffe97f;
  opacity: 0.4;

  user-select: none;
}

.error-page__cta {
  margin-top: 20px;
  background-color: var(--primary);

  padding: 15px;
  width: 200px;
  cursor: pointer;

  font-family: "Lato";
  font-style: normal;
  font-weight: 300;
  font-size: 15px;
  line-height: 18px;

  text-align: center;
  letter-spacing: 0.05em;

  color: var(--secondary);

  opacity: 0.8;
}

.error-page .header__version-container {
  height: 70px;
  width: 70px;
  background-color: var(--primary);
  opacity: 0.5;
  padding-right: 5px;
}

.error-page .version__text {
  font-size: 32px;
}
