.todo-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 60vw;
  border-top: 1px solid var(--secondary);
}

.task-progress__container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.todo-task {
  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 300;
  font-size: 24px;
  line-height: 30px;
  letter-spacing: 0.07em;

  color: #0e1111;

  margin: 0;
  padding: 20px 0;
  position: relative;
}

.todo-progress {
  width: 20px;
  height: 20px;

  transition: box-shadow 0.3s;

  cursor: pointer;

  appearance: none;
  -webkit-appearance: none;
  border: 1.5px solid var(--secondary);
  background-color: transparent;
  border-radius: 2px;

  margin-right: 50px;
}

.todo-progress:checked {
  box-shadow: inset 0 0 0 20px var(--secondary);
}

.todo-task.fave::before {
  content: "";
  position: absolute;
  height: 10px;
  width: 10px;
  border-radius: 50%;
  background-color: var(--accent-1);
  left: -1.5rem;
  top: 2rem;
}

.edit-button,
.delete-button {
  border: 0;
  background-color: transparent;
  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 300;
  font-size: 15px;
  line-height: 19px;
  /* identical to box height */

  cursor: pointer;

  letter-spacing: 0.07em;

  color: #000000;
}

.delete-button {
  margin-left: 30px;
}

@media only screen and (max-width: 435px) {
  .todo-item {
    width: 90vw;
  }

  .todo-task {
    font-size: 14px;
  }
}

@media only screen and (max-width: 768px) {
  .todo-item {
    width: 80vw;
  }

  .todo-task {
    font-size: 14px;
  }
  .todo-progress {
    margin-right: 40px;
  }

  .edit-button,
  .delete-button {
    font-size: 12px;
  }
}
