.auth-page {
  display: flex;
  align-items: center;
  /* margin-top: 120px; */
  flex-direction: column;
  height: 100vh;
  width: 100vw;

  background-color: var(--secondary);

  left: 0;
  position: absolute;
}

.auth__header__container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.auth__header__text {
  font-style: normal;
  font-weight: 700;
  font-size: 96px;
  line-height: 70px;
  letter-spacing: 0.215em;
  color: var(--primary);
}
.auth__header__dot {
  font-style: normal;
  font-weight: 800;
  font-size: 96px;
  line-height: 100%;

  color: var(--accent-1);
}

.auth-page .nav-1 {
  position: relative;
  margin-bottom: 40px;
  height: 40px;
}
@media only screen and (max-width: 435px) {
  .auth__header__container {
    margin: 10px 0;
  }
  .auth__header__text,
  .auth__header__dot {
    margin-top: 50px;
    letter-spacing: 0.05em;
  }
  .register.auth__header__text,
  .register.auth__header__dot {
    font-size: 56px;
    line-height: 21px;
  }

  .nav-1 {
    height: 40px;
  }
  .nav-1__link {
    font-size: 18px;
    padding-left: 12px;
  }
  .auth-page {
    justify-content: flex-start;
  }
}

@media (min-aspect-ratio: 16/10) and (max-height: 800px) {
  .auth__header__dot,
  .auth__header__text {
    font-size: 56px;
    line-height: 50px;
  }
  .auth-page .nav-1 {
    margin-bottom: 20px;
  }
}
