.todo-page {
  min-height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;

  background-color: var(--primary);

  position: absolute;
  left: 0;
}

.todo-filter-group {
  width: 40vw;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding-bottom: 20px;
}

.options__container,
.options__item {
  width: 140px;

  margin: 0;

  background-color: var(--accent-2);

  border: 0;
  outline: 0;

  font-family: "Merriweather Sans";
  padding: 10px;

  font-family: "Lato";
  font-style: normal;
  font-weight: 300;
  font-size: 15px;
  line-height: 18px;
  cursor: pointer;

  text-align: center;
  letter-spacing: 0.05em;

  color: #0e1111;

  margin: 0;

  text-transform: uppercase;
  border: var(--secondary) 1px solid;
}

@media only screen and (max-width: 435px) {
  .todo-filter-group {
    width: 78vw;
  }
}
