.pomo-item {
  display: flex;
  background-color: var(--accent-2);
  width: 40vw;
  justify-content: space-between;

  border: 1px solid var(--secondary);

  margin-bottom: 20px;
}

.pomo-item__right {
  flex-grow: 1;
}

.pomo-item__title-delete {
  padding: 10px 0 10px 3vw;

  display: flex;
  justify-content: space-between;
  width: 100%;

  border-bottom: 1px solid var(--secondary);

  padding-bottom: 10px;
}

.pomo-item__title {
  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 300;
  font-size: 24px;
  line-height: 30px;
  letter-spacing: 0.07em;

  /* Secondary Color */

  color: #0e1111;

  position: relative;
}

.pomo-item__delete {
  border: 0;
  background-color: transparent;
  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 25px;
  letter-spacing: 0.07em;
  /* identical to box height */

  cursor: pointer;

  letter-spacing: 0.07em;

  color: #000000;
}

.pomo-item__value {
  padding: 10px 0 10px 3vw;

  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.07em;

  color: #0e1111;
}

.pomo-item__title.fave::before {
  content: "";
  height: 12px;
  width: 12px;
  border-radius: 50%;
  position: absolute;

  left: -2vw;
  top: 0.6vw;

  background-color: red;
}

.pomo-item__play-container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--secondary);
  border: 0;
  width: 90px;
  cursor: pointer;
  padding: 0;
  border-right: 1px var(--secondary) solid;
}

.pomo-item__play {
  color: var(--primary);
  height: 30px;
  width: 30px;
}

.pomo-item__play-container:hover {
  background-color: var(--primary);
  color: var(--secondary);
}
.pomo-item__play-container:hover .pomo-item__play {
  color: var(--secondary);
}

@media only screen and (min-width: 1980px) {
  .pomo-item__title.fave::before {
    left: -1vw;
    top: 0.3vw;
  }
}

@media only screen and (max-width: 820px) {
  .pomo-item {
    width: 80vw;
  }

  .pomo-item__title.fave::before {
    left: -2vw;
    top: 1vw;
  }
}

@media only screen and (max-width: 435px) {
  .pomo-item {
    width: 90vw;
  }

  .pomo-item__title-delete {
    padding: 5px 0 3px 8vw;
  }
  .pomo-item__title {
    font-size: 14px;
  }

  .pomo-item__title.fave::before {
    left: -5vw;
    top: 2vw;
  }

  .pomo-item__value {
    padding: 5px 0 3px 8vw;
    font-size: 12px;
    color: #0e1111;
  }
  .pomo-item__value {
    font-size: 8px;
  }
}
