body {
  overflow: hidden;
}

.dashboard-page {
  background: var(--secondary);
  height: 100vh;
  width: 100vw;
  overflow: hidden;

  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--primary);

  position: absolute;
  left: 0;
}

.dashboard__nav {
  background-color: var(--secondary);
}

.dashboard-page .nav-2 {
  border-color: var(--primary);
}

.dashboard-page .nav-2__close {
  color: var(--primary);
  font-family: "Lato";
  font-style: normal;
  font-weight: 300;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  letter-spacing: 0.715em;
}

.dashboard__header {
  /* padding-top: 50px; */
  height: auto;
  background-color: var(--secondary);
  display: flex;
  justify-content: space-between;
  width: 100vw;
  padding: 50px 10vw 50px 10vw;
}

.header__image-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header__image-edit {
  color: var(--primary);
  border: 1px solid var(--primary);
  font-family: "Lato";
  font-style: normal;
  font-weight: 300;
  font-size: 10px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.265em;
  background-color: transparent;
  text-align: center;
  padding: 5px;
  border-radius: 2px;
  margin-top: 10px;
  cursor: pointer;
}

.header__image-edit:hover {
  background-color: var(--primary);
  color: var(--secondary);
  border: 1px solid var(--secondary);
}

.header__image {
  height: 150px;
  width: 150px;
  border-radius: 50%;
  filter: saturate(0%);
  object-fit: cover;
}

.header__text-container {
  padding: 0 4vw;
  flex-grow: 1;
}
.text__welcome {
  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 800;
  font-size: 96px;
  line-height: 100%;
  letter-spacing: -0.025em;
  color: var(--primary);
}
.text__welcome::after {
  content: ".";
  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 800;
  font-size: 128px;
  color: var(--accent-1);
}
.text__quotes-container {
  margin-top: 20px;
}
.quotes-quote,
.quotes-author {
  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 197%;
  letter-spacing: 0.07em;
  color: var(--accent-2);
}
.quotes-quote {
  text-align: justify;
}
.quotes-author {
  text-align: right;
}

.header__version-container {
  height: 120px;
  width: 120px;
  background-color: var(--accent-1);
  border-radius: 50%;

  display: flex;
  justify-content: center;
  align-items: center;
}

.version__text {
  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 800;
  font-size: 64px;
  line-height: 80px;
  text-align: center;
  letter-spacing: 0.265em;
  padding-left: 1rem;

  color: #000000;

  user-select: none;
}

.dashboard__body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 100vw;
  background-color: var(--primary);

  flex-grow: 1;
}

.body__mode-container {
  width: 80vw;
  display: flex;

  align-items: center;
  /* justify-content: center; */

  /* border-bottom: 1px solid var(--secondary); */
  position: relative;

  height: 120px;
  cursor: pointer;
}

/* bottom line */
.body__mode-container::before {
  content: "";
  width: 100%;
  position: absolute;
  bottom: 18px;
  border-top: 1px solid var(--secondary);

  z-index: -1;
}
/* upper lines */
.body__mode-container::after {
  content: "";
  height: 52px;
  width: 100%;
  position: absolute;
  bottom: 82px;
  border-top: 1px solid var(--secondary);
  border-bottom: 1px solid var(--secondary);
  /* background-color: rgba(255, 0, 0, 0.153); */
}

.mode__link,
.mode__total {
  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 300;
  font-size: 128px;
  line-height: 161px;
  letter-spacing: 0.07em;
  color: #0e1111;
  text-decoration: none;
}

.mode__link {
  padding-left: 15vw;
  font-size: 118px;
}

.mode__link-highlight {
  content: "";
  background-color: #e3211759;
  height: 40px;
  width: 720px;

  bottom: 40px;
  position: absolute;
  z-index: -1;
  display: absolute;
  left: 20px;
}

.body__mode-container:hover div .mode__link-highlight {
  scale: 2;
  transition: all 2s;
}

.mode__total::after {
  content: ".";
  font-weight: 800;
  font-size: 120px;

  letter-spacing: -0.025em;
  color: #e32017;
}

.dashboard__footer {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--accent-2);
  padding: 20px;
  width: 100vw;
  z-index: 1;
}

.footer__graph-container {
  display: flex;
  width: 80vw;
  height: 10vh;
  border: 1px solid var(--secondary);
}

.graph__item.notes {
  background-color: var(--accent-1);
}
.graph__item.todo {
  background-color: var(--secondary);
}
.graph__item.pomo {
  background-color: var(--primary);
}

.footer__legend-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
}

.legend__item-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.item__disp-text {
  padding-left: 20px;
  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 234%;
  text-align: right;
  letter-spacing: 0.07em;
  color: #0e1111;
}

.item__color {
  height: 20px;
  width: 30px;
  margin-left: 10px;
}

.item__color.notes {
  background-color: var(--accent-1);
}

.item__color.todo {
  background-color: var(--secondary);
}
.item__color.pomo {
  background-color: var(--primary);
}

@media only screen and (max-width: 1024px) {
  .text__welcome {
    font-size: 70px;
  }
  .header__text-container {
    margin: 0 4vw;
    flex-grow: 1;
  }

  .header__version-container {
    height: 120px;
    width: 120px;
  }

  .mode__link {
    padding-left: 1vw;
  }
}

@media only screen and (max-width: 900px) {
  .dashboard__header {
    width: 100vw;
    padding: 20px 5vw 0 5vw;
  }

  .header__image {
    height: 120px;
    width: 120px;
  }

  .header__text-container {
    padding: 0 1vw 2vh 1vw;
    flex-grow: 1;
  }
  .text__welcome {
    font-size: 50px;
    line-height: 90%;
  }

  .header__version-container {
    height: 120px;
    width: 120px;
  }

  .mode__link,
  .mode__total {
    padding-left: 0vw;
    letter-spacing: 0em;
    font-size: 90px;
  }

  .body__mode-container::before {
    bottom: 22px;
  }
  /* upper lines */
  .body__mode-container::after {
    height: 52px;

    bottom: 82px;

    border-bottom: 1px solid var(--secondary);
    /* background-color: rgba(255, 0, 0, 0.153); */
  }
}

@media only screen and (max-width: 433px) {
  .dashboard-page .nav-2 {
    height: 50px;
  }
  .dashboard-page .nav-2__close {
    height: 50px;
    font-size: 14px;
    margin: 0;
  }
  .dashboard__header {
    flex-direction: column;

    height: auto;

    justify-content: center;
    align-items: flex-start;
    width: 100vw;
    padding: 10px 5vw 0 5vw;
  }
  .header__image-container {
    flex-direction: row;

    align-items: center;
    padding: 0 5vw 0 5vw;
  }
  .header__image {
    top: 100px;
    right: 50px;
    height: 50px;
    width: 50px;
    margin-right: 20px;
  }
  .header__text-container {
    padding: 0;
    flex-grow: 1;
  }
  .text__welcome {
    font-size: 40px;
  }

  .header__version-container {
    height: 30px;
    width: 30px;
    margin-bottom: 10px;
    margin-left: 5vw;
  }
  .version__text {
    font-size: 14px;
    padding-left: 0rem;
  }

  .quotes-quote,
  .quotes-author {
    font-size: 12px;
  }

  .dashboard__body {
    padding-top: 1vh;
    width: 100vw;
  }
  .body__mode-container {
    width: 100vw;

    justify-items: center;
    align-items: center;
  }

  .mode__link,
  .mode__total {
    font-size: 54px;
    line-height: 161px;
  }
  .mode__total::after {
    font-size: 58px;
  }

  .body__mode-container {
    height: 80px;
    padding-left: 5vw;
    width: 90vw;
  }

  .graph__item {
    height: 100%;
  }

  /* bottom line */
  .body__mode-container::before {
    bottom: 22px;
    width: 80vw;
  }
  /* upper lines */
  .body__mode-container::after {
    height: 22px;
    width: 80vw;
    bottom: 50px;
  }

  .mode__link-highlight {
    content: "";
    background-color: #e3211725;

    width: 70vw;

    bottom: 30px;
    height: 30px;
    left: 5vw;
  }
}

@media (min-aspect-ratio: 16/10) and (max-height: 800px) {
  .text__welcome {
    font-size: 54px;
  }

  .mode__link,
  .mode__total,
  .mode__total::after {
    font-size: 80px;
  }

  .body__mode-container {
    height: 40px;
    padding-left: 5vw;
    width: 90vw;
  }

  /* bottom line */
  .body__mode-container::before {
    bottom: -5px;
    width: 80vw;
  }
  /* upper lines */
  .body__mode-container::after {
    /* display: none; */
    height: 30px;
    width: 80vw;
    bottom: 32px;
  }

  .mode__link-highlight {
    content: "";
    background-color: #e3211759;

    height: 30px;
    width: 20vw;
    left: -20px;
    bottom: -0px;

    left: 20vw;
  }
}
