.pomo-single {
  background-color: var(--primary);
  height: 100vh;
  width: 100vw;

  display: flex;
  flex-direction: column;
  align-items: center;

  position: absolute;
  left: 0;
}

.pomo-single.rest {
  background-color: var(--accent-2);
  height: 100vh;
  width: 100vw;

  display: flex;
  flex-direction: column;
  align-items: center;

  position: absolute;
  left: 0;
}

.pomo-single__title {
  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 96px;
  line-height: 121px;
  /* identical to box height */

  text-align: center;
  letter-spacing: -0.02em;

  /* Secondary Color */

  color: #0e1111;
  position: relative;
}

.fave.pomo-single__title::before {
  content: "";
  position: absolute;
  left: -2rem;
  top: 3rem;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background-color: var(--accent-1);
}

.pomo-single__value {
  text-transform: uppercase;
  font-weight: 300;
  font-size: 16px;
  line-height: 30px;
  text-align: right;
  letter-spacing: 0.07em;
  color: #0e1111;
}

.pomo-single .subheader__container {
  display: flex;
  justify-content: space-between;
  align-items: center;

  width: 80vw;

  border-bottom: 1px var(--secondary) solid;
  border-top: 1px var(--secondary) solid;
  padding: 1rem 0;
}

.pomo.edit-button {
  border: 1px solid #0e1111;
  background-color: transparent;
  width: 125px;
  height: 40px;
  font-family: "Lato";
  font-style: normal;
  font-weight: 300;
  font-size: 15px;
  line-height: 18px;
  text-align: center;
  letter-spacing: 0.19em;
  color: #0e1111;
}
.pomo.edit-button:hover {
  background-color: #0e1111;
  color: var(--primary);
}

.pomo.form-date {
  font-style: italic;
  text-transform: uppercase;
  font-weight: 300;
  font-size: 24px;
  line-height: 30px;
  text-align: right;
  letter-spacing: 0.07em;
  color: #0e1111;
  margin: 0;
}

.body__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.all-timer__container {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  width: 90vw;
}
.timer__time__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: "Lato";
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  /* identical to box height */

  text-align: center;
  letter-spacing: 0.05em;

  /* Secondary Color */

  color: #0e1111;
}

.options__container.timer {
  margin: 10px 3px;
}

.edit__error {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  padding: 20px;
  position: absolute;
  background-color: rgba(9, 9, 9, 0.651);
  bottom: 420px;
  border-radius: 5px;
  width: 350px;
}

.edit__error div {
  background-color: transparent;
  font-size: 12px;
  letter-spacing: 0.1em;
  font-weight: 100;
  color: var(--accent-1);
}

@media only screen and (max-width: 435px) {
  .edit__error {
    bottom: 120px;
  }

  .pomo-single__title {
    font-size: 36px;
    line-height: 50px;
  }

  .fave.pomo-single__title::before {
    left: -1rem;
    top: 40%;
    height: 10px;
    width: 10px;
  }

  .pomo.form-date {
    font-size: 14px;
  }

  .pomo.edit-button {
    width: 100px;
  }
}

@media (min-aspect-ratio: 4/3) and (max-height: 1000px) {
  .pomo-single__title {
    font-size: 66px;
    line-height: 121px;
  }

  .pomo.form-date {
    font-size: 22px;
  }
}
