.note-item {
  display: flex;
  flex-direction: column;

  width: 50vw;

  position: relative;
}

.fave.note-item::before {
  content: "";
  height: 10px;
  width: 10px;
  border-radius: 50%;
  background-color: red;
  color: var(--accent-1);
  position: absolute;
  left: 5%;
  top: 15%;
}

.note-item__title {
  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 300;
  font-size: 36px;
  line-height: 45px;
  letter-spacing: 0.07em;

  padding: 0 5vw;

  /* Secondary Color */

  color: #0e1111;

  text-decoration: none;

  width: 100%;

  border-bottom: 1px var(--secondary) solid;

  cursor: pointer;
}

.note-item__paragraph {
  margin: 5px 0;

  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.27em;

  /* Secondary Color */

  border-bottom: 1px var(--secondary) solid;
  color: var(--secondary);

  padding: 1vh 5vw 2vh 5vw;
  cursor: pointer;
}

.note-item__delete {
  position: absolute;
  right: 5%;
  top: 15%;
  color: var(--secondary);
  background-color: transparent;
  border: 0;

  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  cursor: pointer;
}

@media only screen and (max-width: 435px) {
  .note-item {
    display: flex;
    flex-direction: column;
    width: 80vw;
    position: relative;
  }

  .fave.note-item::before {
    left: 0%;
    top: 18%;
  }

  .note-item__title {
    font-size: 26px;
  }

  .note-item__paragraph {
    font-size: 12px;
  }
}
