.edit-form.dashboard {
  width: 100vw;
  height: 100vh;
  overflow: hidden;

  background-color: rgba(0, 0, 0, 0.884);
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;

  z-index: 5;
}

.edit-image {
  width: 280px;
  height: 280px;
  object-fit: cover;

  background-color: #f7e173;
  border: 1px solid var(--secondary);
}

.edit-input__input {
  margin: 20px 0;
  width: 280px;
  color: var(--secondary);
  font-weight: 300;
  font-size: 8px;
  line-height: 20px;
  letter-spacing: 0.27em;
  text-transform: uppercase;
}

.edit-input__input::file-selector-button {
  background-color: var(--primary);
  color: var(--secondary);
  border: 1px solid var(--secondary);
  font-family: "Merriweather Sans";
  letter-spacing: 0.07em;

  font-weight: 100;
  font-size: 12px;
  line-height: 20px;
}

.dashboard .edit__error {
  bottom: 10vh;
}

@media only screen and (max-width: 428px) {
  .dashboard .edit__error {
    bottom: 8vh;
  }
}

@media (min-aspect-ratio: 16/10) and (max-height: 800px) {
  .dashboard .edit__error {
    bottom: 30px;
  }
}
