.auth-form-container {
  display: flex;
  flex-direction: column;
  margin: 0;
  position: relative;
}

.auth-form {
  display: flex;
  flex-direction: column;

  justify-content: center;

  font-size: 15px;
}

.auth-form__subtext__container {
  display: flex;
  justify-content: center;

  margin: 30px 0;
}

.auth-form__subtext__text {
  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.35em;
  color: var(--accent-2);
}
.auth-form__subtext__link {
  text-decoration: none;
  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 800;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.25em;
  color: #ffd300;
}

.auth-form__subtext__link:hover {
  filter: brightness(85%);
}

.form__label {
  position: relative;
}

.auth-form .form__input {
  padding: 12px;
}

.form__btn-showPassword {
  background-color: transparent;
  text-decoration: none;
  position: absolute;
  background-color: transparent;
  color: var(--primary);
  outline: none;
  border: none;
  right: 10px;
  top: 47px;

  font-size: 25px;
  cursor: pointer;
}

.form__input.password:focus + .form__btn-showPassword {
  color: var(--secondary);
}

.error__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  padding: 20px;
  position: absolute;
  background-color: rgba(9, 9, 9, 0.651);
  bottom: -180px;
  border-radius: 5px;
  height: 150px;
}

.error__container p {
  background-color: transparent;
  font-size: 12px;
  letter-spacing: 0.1em;
  font-weight: 100;
  color: var(--accent-1);
}

.auth-image {
  height: 120px;
  width: 120px;
  border-radius: 50%;
  align-self: center;
}

.auth-form .edit-input__input {
  align-self: center;
  background-color: rgba(128, 128, 128, 0.176);
  color: var(--accent-2);
  width: 200px;
  border-radius: 2px;
}

@media only screen and (max-width: 435px) {
  .auth-form__subtext__container {
    justify-content: flex-start;
    margin: 0 0 10px 0;
    width: 90vw;
  }
  .auth-form__subtext__text {
    font-size: 12px;
    letter-spacing: 0.35em;
  }
  .auth-form__subtext__link {
    font-size: 12px;
    letter-spacing: 0.35em;
  }
  .auth-form .form__label {
    font-weight: 300;
    letter-spacing: 0.25em;
    font-size: 12px;
  }
  .auth-form .form__input {
    margin: 10px 0 18px 0;
    padding: 12px;
  }
  .auth-form .form__btn--submit {
    margin: 0;
  }

  .form__btn-showPassword {
    right: 10px;
    top: 40px;

    font-size: 25px;
  }

  .auth-form .edit-input__input {
    margin: 10px 0;
  }
}

@media (min-aspect-ratio: 16/10) and (max-height: 800px) {
  .auth-form__subtext__container {
    justify-content: flex-start;
    margin: 10px 0 10px 0;
  }
  .auth-form .form__input {
    margin: 10px 0 18px 0;
    padding: 12px;
  }

  .auth-form .form__btn--submit {
    font-size: 14px;
    line-height: 19px;

    height: 50px;
    transition: box-shadow 0.15s, transform 0.15s;
    margin: 0;
    color: var(--secondary);
    border: none;
    border-radius: 4px;
  }

  .auth-form .edit-input__input {
    margin: 10px 0;
  }

  .auth-image {
    height: 100px;
    width: 100px;
    border-radius: 50%;
  }

  .error__container {
    padding: 20px 10px;
    position: absolute;
    background-color: rgba(9, 9, 9, 0.651);
    bottom: -155px;
    border-radius: 5px;
    height: 150px;
  }
}
