.note-page {
  min-height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;

  background-color: var(--primary);

  position: absolute;
  left: 0;
}

.nav-2 {
  background-color: transparent;
  margin: 0 10vw;
  height: 70px;
  display: flex;
  border-bottom: solid 1px var(--secondary);
  width: 80vw;
  align-items: flex-end;
  justify-content: flex-end;
}

.note-add-button {
  height: 90px;
  width: 90px;
  border-radius: 50%;
  outline: 0;
  border: 1px solid var(--secondary);

  position: fixed;
  bottom: 10%;
  right: 10%;

  color: var(--primary);
  background-color: var(--secondary);
  z-index: 1;
}

@media only screen and (max-width: 435px) {
  .page__title {
    font-weight: 700;
    font-size: 54px;
    line-height: 126px;
    letter-spacing: 0.19em;
  }

  .note-filter__container {
    display: flex;
    justify-content: center;
    align-items: center;

    margin-bottom: 15px;
    width: 80vw;
  }
}
