.pomodoro-timer__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.pomodoro-timer__container .container {
  width: 400px;
}

.container .CircularProgressbar-path {
  filter: drop-shadow(0px 1px 1px rgba(0, 0, 0, 0.24));
  stroke-width: 1px;
  stroke: var(--secondary);
}

.container .CircularProgressbar-trail {
  filter: drop-shadow(rgba(0, 0, 0, 0.05) 0px 1px 2px 0px);
  stroke: #ffea7f91;
  stroke-width: 1px;
}

.container .CircularProgressbar-text {
  fill: var(--secondary);

  font-size: 22px;
  font-weight: 500;

  letter-spacing: 0.1em;
}

.timer__buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 50px;
  width: 80vw;
}

.timer__button {
  font-family: "Lato";
  font-style: normal;
  font-weight: 300;
  font-size: 15px;
  line-height: 18px;

  text-align: center;
  letter-spacing: 0.05em;

  color: #0e1111;

  width: 200px;
  height: 40px;
  left: 718px;
  top: 769px;

  border: 1px solid #0e1111;
  background-color: transparent;

  cursor: pointer;
}

.timer__button:hover {
  background-color: #0e1111;
  color: var(--primary);
}

.timer-status {
  font-family: "Merriweather Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 46px;
  line-height: 121px;
  /* identical to box height */

  text-align: center;
  letter-spacing: -0.02em;

  /* Secondary Color */

  color: #0e1111;
}

@media only screen and (max-width: 435px) {
  .pomodoro-timer__container .container {
    width: 200px;
  }

  .timer-status {
    font-size: 26px;
    line-height: 51px;
  }
}

@media (min-aspect-ratio: 4/3) and (max-height: 1000px) {
  .pomodoro-timer__container .container {
    width: 200px;
  }

  .timer__buttons {
    margin-top: 20px;
  }

  .timer-status {
    font-size: 26px;
    line-height: 80px;
  }
}
